<script lang="ts">
  import { Swipe, SwipeItem } from "svelte-swipe";
  import Card from "../card.svelte";
  import Markdown from "./Pedal.md";

  import { fade, fly } from "svelte/transition";
  import { fade1, flyLeft } from "../stores";

  const swipeConfig = {
    defaultIndex: 0,
    showIndicators: true,
    autoplay: false,
    delay: 4000,
    transitionDuration: 500,
  };

  let paths: Array<string> = [];
  for (let i = 0; i < 9; i++) {
    let path = `img/projects/Pedal/footp${i}.jpg`;
    paths.push(path);
  }
</script>

<div class="markdown" in:fly={flyLeft}>
  <div class="card-wrapper">
    <Card width="fit-content">
      <div class="swipe-holder" in:fade={fade1}>
        <Swipe {...swipeConfig}>
          {#each paths as path}
            <SwipeItem>
              <img srcset={path} alt="" />
            </SwipeItem>
          {/each}
        </Swipe>
      </div>
    </Card>
  </div>
  {@html Markdown}
</div>

